body {
	background-color: lightyellow;
	color: rgb(14, 14, 41);
	font-family: "Courier New", Courier, monospace;
	font-size: large;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

header {
	width: fit-content;
	font-size: 2rem;
}

a:visited {
	color: coral;
}

a {
	color: rgb(168, 70, 53);
}

.App {
	display: flex;
	flex-direction: column;
	padding: 2rem;
	justify-content: space-between;
}

.project-card-container {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	row-gap: 3rem;
	column-gap: 1.5rem;
	justify-content: space-evenly;
}

.title-bar {
	padding: 1rem;
	width: 98%;
	margin-bottom: 3rem;
	padding-top: 2rem;
	padding-left: 2rem;
}

.project-card {
	background-color: rgba(250, 250, 210, 0.678);
	width: 25%;
	height: 17rem;
	padding: 2rem;
	box-shadow: 0 0 10px rgb(233, 233, 173);
}

.project-card:hover {
	background-color: rgb(245, 245, 195);
}

#bio-line {
	padding-top: 0.5rem;
	font-size: 1rem;
	padding-bottom: 0.5rem;
}

#bio-link-container {
	font-size: 1rem;
}

#projectDescription {
	padding-top: 1.3rem;
}

#bio-link {
	margin-right: 10px;
}

#repo-link {
	font-size: 1rem;
	color: darksalmon;
	font-weight: lighter;
	text-decoration: none;
}

#repo-link:hover {
	color: firebrick;
}

/* vvv background colours  for visualising screen width changes vvv */

@media only screen and (max-width: 1300px) {
	/* body {
    background-color: yellow;
  } */
	.project-card {
		width: 40%;
	}
}
@media only screen and (max-width: 1000px) {
	/* body {
    background-color: brown;
  } */
	.project-card {
		width: 100%;
	}
}
@media only screen and (max-width: 900px) {
	/* body {
    background-color: yellow;
  } */
	.project-card {
		width: 100%;
	}
}

